<template>
  <div>
    <div id="weekdayStr"></div>
    <div
      id="weeklyPeriod"
      @touchstart.stop=""
      @click.stop="touchendPeriod"
      @hold.stop="holdPeriod"
      @swiping.stop="swipingPeriod"
      @swipeend.stop="swipeendPeriod"
    ></div>
  </div>
</template>

<script>
import $ from "jquery";
import Vue from "vue";
import VueTouch from "vue-touchjs";
Vue.use(VueTouch);
export default {
  props: {
    weeklyData: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  data() {
    return {
      mouseDownIsSelected: false,
      isReadyToSelect: false,
      positionStartY: 0,
      positionEndY: 0,
    };
  },
  created() {},
  async mounted() {
    let self = this;

    await this.initWeekDay();
    this.getWeeklyData(this.weeklyData);

    let periodItem = document.getElementsByClassName("weekly-period-item");

    for (let i = 0; i < periodItem.length; i++) {
      let index = i;
      periodItem[index].onclick = null;
      periodItem[index].onclick = () => {
        if (periodItem[index].classList.contains("item-selected")) {
          periodItem[index].classList.remove("item-selected");
        } else {
          periodItem[index].classList.add("item-selected");
        }
      };
    }
    self.formatSelectedTime();
  },
  methods: {
    getWeeklyData(data) {
      if (data === undefined) {
        this.weeklyData = [];
      } else {
        this.weeklyData = data;
      }

      this.initTimeData();
    },
    initWeekDay() {
      $("#weeklyPeriod").children().remove();
      $("#weekdayStr").children().remove();
      for (let i = 0; i < 7; i++) {
        let weekDay =
          "" + '<div class="weekday">' + this.getWeekDayPeriod(i) + "</div>";
        let weekStr =
          "" + '<div class="weekday-str">' + this.getWeekDayStr(i) + "</div>";
        $("#weeklyPeriod").append(weekDay);
        $("#weekdayStr").append(weekStr);
      }
    },
    formatSelectedTime() {
      let data = [];
      // console.log( $(".weekday-period"))
      for (let i = 0; i < 7; i++) {
        let day = $(".weekday-period").eq(i);
        let start = -1,
          end = -1,
          hasLast = false;
        for (let j = 0; j < 30; j++) {
          let item = day.children().eq(j);
          if (item.hasClass("item-selected")) {
            if (start === -1) {
              hasLast = true;
              start = j;
              end = j;
            } else {
              if (j - end === 1) {
                end = j;
                hasLast = true;
              } else {
                data.push({
                  timePeriod: {
                    from: {
                      hour: parseInt(
                        day.children().eq(start).children().eq(0).data("hour")
                      ),
                      minute: parseInt(
                        day.children().eq(start).children().eq(0).data("minute")
                      ),
                    },
                    to: {
                      hour:
                        end === 29
                          ? 22
                          : parseInt(
                              day
                                .children()
                                .eq(end + 1)
                                .children()
                                .eq(0)
                                .data("hour")
                            ),
                      minute:
                        end === 29
                          ? 30
                          : parseInt(
                              day
                                .children()
                                .eq(end + 1)
                                .children()
                                .eq(0)
                                .data("minute")
                            ),
                    },
                  },
                  dayOfWeek: i,
                });
                start = -1;
                end = -1;
                hasLast = false;
                j--;
              }
            }
          }
        }

        if (hasLast) {
          data.push({
            timePeriod: {
              from: {
                hour: parseInt(
                  day.children().eq(start).children().eq(0).data("hour")
                ),
                minute: parseInt(
                  day.children().eq(start).children().eq(0).data("minute")
                ),
              },
              to: {
                hour:
                  end === 29
                    ? 22
                    : parseInt(
                        day
                          .children()
                          .eq(end + 1)
                          .children()
                          .eq(0)
                          .data("hour")
                      ),
                minute:
                  end === 29
                    ? 30
                    : parseInt(
                        day
                          .children()
                          .eq(end + 1)
                          .children()
                          .eq(0)
                          .data("minute")
                      ),
              },
            },
            dayOfWeek: i,
          });
          start = -1;
          end = -1;
        }
      }

      // console.log(data)
      this.$emit('saveWeeklyBasisData', data)
      //   if (window.webkit !== undefined) {
      //     window.webkit.messageHandlers.saveData.postMessage({
      //       data: JSON.stringify(data),
      //     });
      //   }
    },
    touchendPeriod(ev) {
      this.isReadyToSelect = false;
      this.positionEndY = 0;
      this.positionStartY = 0;

      this.formatSelectedTime();
    },
    holdPeriod(ev) {
      this.isReadyToSelect = true;
      let item = this.initSwipeItem(ev);
      if (item.classList.contains("item-selected")) {
        item.classList.remove("item-selected");
        this.mouseDownIsSelected = true;
      } else {
        item.classList.add("item-selected");
        this.mouseDownIsSelected = false;
      }

      this.formatSelectedTime();
    },
    swipingPeriod(ev) {
      if (this.isReadyToSelect) {
        this.positionEndY = ev.detail.position.y;
        let item = this.initSwipeItem(ev);
        if (this.mouseDownIsSelected) {
          item.classList.remove("item-selected");
        } else {
          item.classList.add("item-selected");
        }
      }
    },
    swipeendPeriod(ev) {
      if (this.isReadyToSelect) {
        this.positionEndY = ev.detail.position.y;
        let item = this.initSwipeItem(ev);
        if (this.mouseDownIsSelected) {
          item.classList.remove("item-selected");
        } else {
          item.classList.add("item-selected");
        }
        this.positionEndY = 0;
        this.positionStartY = 0;
        this.isReadyToSelect = false;
        this.formatSelectedTime();
      }
    },
    initSwipeItem(ev) {
      let item,
        index = 0,
        parentIndex = 0,
        currerntIndex = 0;
      if (ev.target.nodeName === "DIV") {
        if (
          ev.target.className === "weekly-period-time-full-line" ||
          ev.target.className === "weekly-period-time-half-line"
        ) {
          index = ev.target.parentNode.attributes["data-index"].nodeValue;
          parentIndex =
            ev.target.parentNode.parentNode.attributes["data-index"].nodeValue;
        } else {
          index = ev.target.attributes["data-index"].nodeValue;
          parentIndex = ev.target.parentNode.attributes["data-index"].nodeValue;
        }
      } else {
        if (ev.target.className === "weekly-period-time") {
          index = ev.target.offsetParent.attributes["data-index"].nodeValue;
          parentIndex =
            ev.target.offsetParent.parentNode.attributes["data-index"]
              .nodeValue;
        } else {
          index =
            ev.target.offsetParent.offsetParent.attributes["data-index"]
              .nodeValue;
          parentIndex =
            ev.target.offsetParent.offsetParent.parentNode.attributes[
              "data-index"
            ].nodeValue;
        }
      }
      currerntIndex =
        parseInt(index) +
        parseInt((this.positionEndY - this.positionStartY) / 24);
    //   item = $(".weekday-period").eq(parentIndex).children().eq(currerntIndex);
      item = document.getElementsByClassName("weekday-period")[parentIndex].children[currerntIndex]
      return item;
    },
    initTimeData() {
      $(".weekly-period-item").removeClass("item-selected");
      let weeklyData = this.weeklyData;
      for (let i = 0; i < weeklyData.length; i++) {
        let from = weeklyData[i].timePeriod.from.hour;
        let to = weeklyData[i].timePeriod.to.hour;
        let length = to - from;
        let startIndex = (from - 8) * 2;
        let endIndex = startIndex + length * 2;
        let day = weeklyData[i].dayOfWeek;
        if (weeklyData[i].timePeriod.from.minute === 30) {
          startIndex++;
        }
        if (weeklyData[i].timePeriod.to.minute === 30) {
          endIndex++;
        }
        let dayElement = $(".weekday-period").eq(day);
        for (let i = startIndex + 1; i <= endIndex; i++) {
          dayElement.children().eq(i).addClass("item-selected");
        }
      }
    },
    getWeekDayStr(num) {
      switch (num) {
        case 0:
          return "Sun";
        case 1:
          return "Mon";
        case 2:
          return "Tue";
        case 3:
          return "Wed";
        case 4:
          return "Thu";
        case 5:
          return "Fri";
        case 6:
          return "Sat";
      }
    },
    getWeekDayPeriod(index) {
      let period =
        "" + '<div class="weekday-period" data-index="' + index + '">';

      for (let i = 0; i < 30; i++) {
        let periodTime = this.getPeriodTime(i);
        let hour = periodTime.hour,
          minute = periodTime.notHalf ? 0 : 30,
          ap = periodTime.ap,
          timeStr = periodTime.hour12;
        period +=
          "" +
          '<div class="weekly-period-item ' +
          (periodTime.notHalf ? "" : "weekly-period-item-half") +
          '" data-index="' +
          i +
          '">' +
          '<span class="weekly-period-time" data-hour="' +
          hour +
          '" data-minute="' +
          minute +
          '">';

        if (periodTime.notHalf) {
          period +=
            "" + "<span>" + timeStr + "<span>" + ap + "</span>" + "</span>";
        }

        period += "</span>";

        if (periodTime.notHalf) {
          period += '<div class="weekly-period-time-half-line"></div>';
        } else {
          period += '<div class="weekly-period-time-full-line"></div>';
        }

        period += "</div>";
      }

      period += "</div>";

      return period;
    },
    getPeriodTime(index) {
      let notHalf = index % 2 === 1;
      let hour = 8,
        ap = "AM";
      let periodTime = {
        hour: hour,
        ap: ap,
        notHalf: notHalf,
        hour12: hour,
      };
      if (notHalf) {
        hour = hour + (index - 1) / 2;
      } else {
        hour = hour + (index - 2) / 2;
      }

      periodTime.hour = hour;

      if (hour === 12) {
        periodTime.hour12 = hour;
        periodTime.ap = "PM";
      } else if (hour > 12) {
        periodTime.hour12 = hour - 12;
        periodTime.ap = "PM";
      } else {
        periodTime.hour12 = hour;
        periodTime.ap = "AM";
      }

      return periodTime;
    },
  },
};
</script>

<style>
@import "../../../assets/css/nucleo/css/weekly.period.css";
</style>